.header {
  width: 100%;
}

.header__head {
  display: flex;
  gap: 20px;
  padding: 12px 0;
}

.head__link {
  font-weight: 600;
  font-size: 14px;
  line-height: 20px;
  transition: 0.25s;
  color: #000;

  &:hover {
    color: var(--second-color);
  }
}

.header__head-border-bottom {
  width: 200%;
  transform: translateX(-25%);
  border-bottom: 1px solid rgb(243, 243, 247);
}

.header__container {
  display: flex;
  flex-direction: column;
  padding: 25px 0;
  gap: 16px;
}

.header__main-block {
  display: flex;
  gap: 40px;
  align-items: center;
}

.header__logo {
  width: 250px;
}

.header__delivery-text {
  font-size: 18px;
  line-height: 1.2;
  font-weight: 500;

  .select-sity {
    color: var(--second-color);
    transition: 0.25s;

    &:hover {
      color: var(--second-color-hover);
    }
  }
}

.header__phone-number {
  font-size: 18px;
  line-height: 1.2;
  font-weight: 500;
  color: #000;
  transition: 0.25s;

  &:hover {
    color: var(--second-color);
  }
}

.header__order {
  color: #fff;
  padding: 8px 20px;
  font-size: 16px;
  line-height: 24px;
  background-color: var(--second-color);
  transition: 0.25s;
  border-radius: 100px;
  margin-left: auto;

  &:hover{
    background-color: var(--second-color-hover);
  }
}

.nav-logo{
  display: none;
}

.nav-menu-line{
  display: flex;
  gap: 20px;
  align-items: center;
}

.nav-list{
  list-style: none;
  display: flex;
  gap: 20px;
}

.nav-list__link{
  font-weight: 600;
  color: #000;
  font-size: 14px;
  transition: 0.25s;

  &:hover{
    color: var(--second-color);
  }
}

.storyboard{
  width: 100%;
  position: relative;
}

.swiper__storyboard{
  width: 100%;
  height: 100%;
}

.storyboard__slide{
  border-radius: 20px;
  overflow: hidden;
  cursor: pointer;
}

.storyboard-bg{
  width: 100%;
  aspect-ratio: 4/5;
  position: relative;
  img{
    position: absolute;
    height: 100%;
    width: 100%;
    object-fit: cover;
    object-position: center center;
  }
}

.storyboard-btn{
  position: absolute;
  top: 50%;
  height: 48px;
  width: 48px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  svg{
    height: 24px;
    width: 14px;
    object-fit: contain;
    use{
      fill: none;
      stroke: var(--second-color);
      stroke-linecap: round;
      stroke-width: 2px;
      stroke-linejoin: round;
      transition: 0.25s;
    }
  }

  &:hover{
    svg{
      use{
        stroke: var(--second-color-hover);
      }
    }
  }

  &.prev{
    transform: translate(-100%, -50%);
    left: 0;
    svg{
      transform: rotate(180deg);
    }
  }

  &.next{
    transform: translate(100%, -50%);
    right: 0;
  }
}
