body.lock {
  overflow: hidden;
}

.popup {
  position: fixed;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  opacity: 0;
  visibility: hidden;
  overflow: hidden;
  transition: all 0.2s ease 0s;
  z-index: 100;
}

.popup::after {
  content: "";
  position: absolute;
  top: -40px;
  left: -40px;
  right: -40px;
  bottom: -40px;
  background: inherit;
  filter: blur(30px);
  z-index: -1;
  overflow: hidden;
}

.popup.open {
  opacity: 1;
  visibility: visible;
}

.popup.open .popup__content {
  opacity: 1;
}

.popup__body {
  min-height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.popup__content {
  position: relative;
  opacity: 0;
  width: 100%;
  // сам поп ап
}

.story-btn{
  position: absolute;
  top: 50%;
  height: 48px;
  width: 48px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  svg{
    height: 32px;
    width: 32px;
    object-fit: contain;
    use{
      fill: none;
      stroke: #fff;
      transition: 0.25s;
    }
  }

  &:hover{
    svg{
      use{
        stroke: var(--second-color);
      }
    }
  }

  &.prev{
    transform: translate(-100%, -50%);
    left: 20%;
    svg{
      transform: rotate(180deg);
    }
  }

  &.next{
    transform: translate(100%, -50%);
    right: 20%;
  }

  &.swiper-button-disabled{
    display: none;
  }
}

.swiper__story{
  width: 50%;
  height: 100%;
  padding: 50px 0;
  position: relative;
}

.slide__story{
  display: flex;
  border-radius: 15px;
  overflow: hidden;
  position: relative;
}

.close-storyboard{
  position: absolute;
  top: 88px;
  right: 10px;
  z-index: 15;
}

.swiper-button-lock{
  display: block;
}

.swiper__story-slide{
  width: 100%;
  height: 100%;
}

.story-fragment-btn{
  z-index: 10;
  position: absolute;
  width: 50%;
  height: 100%;
  top: 0;

  &.prev{
    left: 0;
  }

  &.next{
    right: 0;
  }
}

.slide__story-fragment{
  img{
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
}

.pagination-fragments{
  position: absolute;
  top: 15px !important;
  right: 15px;
  left: 15px !important;
  z-index: 15;
  height: 8px;
  display: flex;
  width: unset !important;

  & > *{
    flex: 1;
  }
}

.swiper-pagination-lock{
  display: flex;
}

.swiper-pagination-bullet:only-child{
  display: block !important;
}

.swiper-pagination-bullet{
  background-color: silver !important;
  border-radius: 20px;
  opacity: 0.8;
  position: relative;
}

.swiper-pagination-bullet-active{
  &::before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    background-color: #fff;
    border-radius: 20px;
    height: 8px;
    z-index: 20;
    width: var(--width-active-bullet, 0%);
  }
}

.swiper-pagination-bullet:has(+ .swiper-pagination-bullet.swiper-pagination-bullet-active){
  background-color: #fff !important;
}
